<template>
  <div class="seller-refunds" style="padding-top: 44px;">
    <backer-header title="退款订单" />
    <scroll clickable>
      <div class="title">
        <h6>是否需要退货</h6>
        <p><van-switch size="0.5rem" inactive-color="#9D9D9D" active-color="#5ED247" v-model="checked" /></p>
      </div>
      <address-link v-if="checked" :address="address" />
      <div class="order">
        <div class="order-no">
          <p>订单编号：{{ order.order_no }}</p>
          <span>申请退款</span>
        </div>
        <order-item class="item" :size-drop="false" :goods="goods[0]" price-show />
      </div>
      <div class="connect">
        <img src="../seller-order-detail/maijia@2x.png" alt="">
        <p>联系买家</p>
      </div>
      <div class="order-intro">
        <p><span>退款原因</span>{{ order.return_info.reason }}</p>
        <p><span>退款金额</span>¥{{ order.return_info.amount }}</p>
        <p><span>申请时间</span>{{ order.return_info.create_time }}</p>
<!--        <p><span>退款编号</span>123456</p>-->
      </div>
      <div class="bottom-btn" @click="submit">接受退款</div>
    </scroll>
  </div>
</template>

<script>
import BackerHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import AddressLink from '../../components/address-link/address-link'
import OrderItem from '../../components/order-item/order-item'
import { post } from '../../common/js/http'
import { getItem, removeItem } from '../../common/js/storage'

export default {
  name: 'seller-refunds',
  components: {
    BackerHeader,
    Scroll,
    AddressLink,
    OrderItem
  },
  data() {
    return {
      order: {},
      goods: {},
      address: {},
      checked: false
    }
  },
  created() {
    this.goods = this.$route.params.goods
    this.order = this.$route.params.order
  },
  activated() {
    if (this.$route.params.goods) {
      this.goods = this.$route.params.goods
      this.order = this.$route.params.order
    }
    const address = getItem('selectAddress')
    if (address) {
      this.address = address
      removeItem('selectAddress')
    }
  },
  methods: {
    submit() {
      const loading = this.$toast.loading()
      if (!this.address.id && this.checked) {
        this.$toast.fail('请选择收货地址')
        return
      }
      post('/shop/order/operateReturn', {
        returnid: this.order.return_info.id,
        operate: 1,
        addressid: this.address.id,
        need_return: this.checked ? 1 : 2
      })
        .then(() => {
          loading.close()
          this.$router.goBack()
        })
        .catch(() => {
          loading.close()
        })
    }
  }
}
</script>

